import $T from '@/common/time.js';
import {mapState} from 'vuex';
export default {
	props: {
		item: Object,
		index: {
			type: Number,
			default: -1
		},
		isdetail: {
			type: Boolean,
			default: false
		}
	},
	filters: {
		formatTime(value) {
			return $T.gettime(value);
		}
	},
	computed: {
		...mapState({
			user: state => state.user
		})
	},
	methods: {
		// 打开个人空间
		openSpace(user_id) {
			uni.navigateTo({
				url: '/pages/user-space/user-space?user_id=' + user_id
			});
		},
		// 关注
		follow() {
			this.checkAuth(() => {
				this.$H
					.post(
						'/follow',
						{
							follow_id: this.item.user_id
						},
						{
							token: true
						}
					)
					.then(res => {
						// 通知更新
						uni.$emit('updateFollowOrSupport', {
							type: 'follow',
							data: {
								user_id: this.item.user_id
							}
						});
					});
			});
		},
		// 进入详情页
		openDetail() {
			// 处于详情中
			if (this.isdetail) return;
			uni.navigateTo({
				url: '../../pages/detail/detail?detail=' + JSON.stringify(this.item)
			});
			// 加入历史记录
			// 取出历史记录
			let list = uni.getStorageSync('history')
			list = list ? JSON.parse(list) : []
			let index = list.findIndex(v=>v.id === this.item.id)
			if(index === -1){
				list.unshift(this.item)
				uni.setStorageSync('history',JSON.stringify(list))
			}
			
		},
		// 顶踩操作
		doSupport(type) {
			this.checkAuth(() => {
				this.$H
					.post(
						'/support',
						{
							post_id: this.item.id,
							type: type === 'support' ? 0 : 1
						},
						{
							token: true,
							native: true
						}
					)
					.then(res => {
						if (res.data.errorCode) {
							return uni.showToast({
								title: res.data.msg,
								icon: 'none'
							});
						}
						console.log('通知父组件');
						// 通知父组件
						uni.$emit('updateFollowOrSupport', {
							type: 'support',
							data: {
								type: type,
								id: this.item.id
							}
						});
					});
			});
		},
		// 评论
		doComment() {
			this.checkAuth(() => {
				if (!this.isdetail) {
					return this.openDetail();
				}
				this.$emit('doComment');
			});
		},
		// 分享
		doShare() {
			if (!this.isdetail) {
				return this.openDetail();
			}
			this.$emit('doShare');
		}
	}
};
